import Vue from 'vue'
import Router from 'vue-router'
import store from '../store.js'
import axios from 'axios'

// Containers
const TheContainer = () => import('@/containers/TheContainer')

// Views
const Dashboard = () => import('@/views/Dashboard')
const Medicines = () => import('@/views/Medicines')
const Batches = () => import('@/views/Batches')
const Suppliers = () => import('@/views/Suppliers')
const Purchases = () => import('@/views/Purchases')
const Sales = () => import('@/views/Sales')
const Receipt = () => import('@/views/Receipt')
const Customers = () => import('@/views/Customers')
const Users = () => import('@/views/Users')
const Cashier = () => import('@/views/Cashier')
const Online = () => import('@/views/OnlineOrders')
const Unit = () => import('@/views/Unit')
const Category = () => import('@/views/Categories')

const Charts = () => import('@/views/charts/Charts')
const Alerts = () => import('@/views/Notifications')
// Views - Components
const Login = () => import('@/views/pages/Login')
const Register = () => import('@/views/pages/Register')

Vue.use(Router)

const router = new Router({
  // https://router.vuejs.org/api/#mode
  linkActiveClass: 'active',
  scrollBehavior: () => ({
    y: 0
  }),
  routes: configRoutes()
})

function configRoutes() {
  return [{
      path: '/',
      redirect: '/pages/login',
      name: 'Home',
      component: TheContainer,
      children: [{
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard,
          meta: {
            requiresAuth: true,
          }
        },
        {
          path: 'charts',
          name: 'Charts',
          component: Charts
        },
        {
          path: 'medicines',
          name: 'Medicines',
          component: Medicines,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'onlineOrders',
          name: 'Online Orders',
          component: Online,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'suppliers',
          name: 'Suppliers',
          component: Suppliers,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'sales',
          name: 'Sales',
          component: Sales,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'receipt',
          name: 'Receipt',
          component: Receipt,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'unit',
          name: 'Unit',
          component: Unit,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'category',
          name: 'Category',
          component: Category,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'cashier',
          name: 'Cashier',
          component: Cashier,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'customers',
          name: 'Customers',
          component: Customers,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'users',
          name: 'Users',
          component: Users,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'purchases',
          name: 'Purchases',
          component: Purchases,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'batches',
          name: 'Batches',
          props: true,
          component: Batches,
          meta: {
            requiresAuth: true,
            admin: true
          }
        },
        {
          path: 'notifications',
          name: 'Notifications',
          component: Alerts,
          meta: {
            requiresAuth: true
          }
        },
      ]
    },
    {
      path: '/pages',
      redirect: '/pages/404',
      name: 'Pages',
      component: {
        render(c) {
          return c('router-view')
        }
      },
      children: [
        {
          path: 'login',
          name: 'Login',
          component: Login,
          meta: {
            requiresAuth: false
          }
        },
        {
          path: 'register',
          name: 'Register',
          component: Register
        }
      ]
    }
  ]
}

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.state.user.id) {
      next()
    } else {
      axios({
          url: 'http://localhost:3000' + '/users/checkauth/' + sessionStorage.getItem('token'),
          method: 'GET'
        })
        .then(resp => {
          if (resp) {
            store.state.user = {
              id: resp.data.user.id,
              user_type: resp.data.user.user_type
            }
            next();
            return
          }
        })
        .catch(() => {
          store.dispatch('logout')
        })
    }
  } else {
    next()
  }
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.admin)) {
    if (store.getters.isAdmin) {
      next()
    } else {
      next(from)
    }
  } else {
    next()
  }
})

export default router