import Vue from 'vue'
import Vuex from 'vuex'
import router from './router/index.js'
import axios from 'axios'

Vue.use(Vuex)

const getDefaultState  =() => {
  return {
    sidebarShow: 'responsive',
    sidebarMinimize: false,
    status: '',
    token: sessionStorage.getItem('token') || '',
    user: {}
  }
}

const state = getDefaultState()

const mutations = {
  toggleSidebarDesktop(state) {
    const sidebarOpened = [true, 'responsive'].includes(state.sidebarShow)
    state.sidebarShow = sidebarOpened ? false : 'responsive'
  },
  toggleSidebarMobile(state) {
    const sidebarClosed = [false, 'responsive'].includes(state.sidebarShow)
    state.sidebarShow = sidebarClosed ? true : 'responsive'
  },
  set(state, [variable, value]) {
    state[variable] = value
  },
  auth_request(state) {
    state.status = 'loading'
  },
  auth_success(state, payload) {
    state.status = 'success'
    state.token = payload.token
    state.user = payload.user
    state.username = payload.username
  },
  auth_error(state) {
    state.status = 'error'
  },
  logout(state) {
    Object.assign(state, getDefaultState())
  }
}

const actions = {
  login({
    commit,state
  }, user) {
    return new Promise((resolve, reject) => {
      commit('auth_request')
      axios({
          url: 'http://localhost:3000/users/login',
          data: user,
          method: 'POST'
        })
        .then(resp => {
          const token = resp.data.token
          const BackEnduser = {
            id: resp.data.user._id,
            user_type: resp.data.user.user_type,
          }
          state.user = BackEnduser;
          const payload = {
            token : token,
            user : BackEnduser
          }
          sessionStorage.setItem('token',token)
          commit('auth_success',payload)
          if(BackEnduser.user_type == 1) router.push({ name: "Dashboard" })
          else router.push({ name: "Cashier" })
          resolve(resp.data.user)
        })
        .catch(err => {
          commit('auth_error')
          sessionStorage.removeItem('token')
          // eslint-disable-next-line no-console
          console.log(err.response)
          reject({
            message: err.response.data,
            err: err
          })
        })
    })
  },
  register({
    commit
  }, user) {
    return new Promise((resolve, reject) => {
      commit('auth_request')
      axios({
          url: process.env.VUE_APP_BASE_URL + '/user/post',
          data: user,
          method: 'POST'
        })
        .then(resp => {
          sessionStorage.removeItem('token')
          resolve(resp)
        })
        .catch(err => {
          commit('auth_error', err)
          sessionStorage.removeItem('token')
          reject(err)
        })
    })
  },
  logout({
    commit
  }) {
      commit('logout')
      sessionStorage.removeItem('token')
      sessionStorage.removeItem('user')
      // window.location.reload();
      router.push({
        name: "Login"
      });
  }
}

const getters = {
  isLoggedIn: state => !!state.token,
  authStatus: state => state.status,
  username: state => state.user.username,
  isAdmin: state => state.user.user_type==1,
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})